.page {
  min-width: 430px;
  height: 100%;

  font-family: @font-page;
  font-size: 12px;
  line-height: 15px;

  color: @color-grey;
  background-color: @color-white;

  // overflow-x: hidden;
  // margin-right: calc(-1 * (100vw - 100%));
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;

  min-height: 100%;
  margin: 0;
}
